<script>
import Navbar from "./components/nav";
import { create } from "domain";
import { createDecipheriv } from "crypto";

export default {
  name: "app",
  data() {
    return {
      language: "",
      isSelect: false
    };
  },
  components: {
    Navbar
  },
  methods: {
    showOverlayModal() {
      this.$modal.show("overlayDialog");
      this.language = localStorage.getItem("lang");
      document
        .getElementById("topNavMenu")
        .classList.replace("bg-white", "navOverlayTransparent");

      document.getElementById("langSwitcher").classList.add("noPointer");
    },
    hideOverlayModal() {
      this.$modal.hide("overlayDialog");
      document
        .getElementById("topNavMenu")
        .classList.replace("navOverlayTransparent", "bg-white");

      document.getElementById("langSwitcher").classList.remove("noPointer");
    },
    // Main phone/home immage changes method.
    languageIsSelect(event) {
      this.isSelect = event;
      console.log("=languageIsSelect =>is select: " + event);

      if (this.isSelect == true) {
        this.language = localStorage.getItem("lang");
        console.log("=languageIsSelect =>lang :" + this.language);
      }
    }
  },
  created() { 
    // Main phone/home immage changes method.
    if (localStorage.getItem("lang") != null) {
      this.language = localStorage.getItem("lang");
    } else {
      this.language = this.$i18n.locale;
    }
    // log
    console.log("-created ->is selected for this session: " + this.isSelect);
    console.log("-created ->localStorage -key-'lang' is-empty: " + (localStorage.getItem("lang") == null));
    console.log("-created -> language: " + this.language);
    
  }
};
</script>

