Component: Navigation Bar

<template>
  <div class="navbar">
    <header>
      <div class="container">
        <b-navbar id="topNavMenu" toggleable="lg" fixed="top" class="bg-white">
          <div class="container">
            <b-navbar-brand>
              <img
                src="../assets/imgs/FA VRB_Logo_V1-02--02.png"
                class="d-inline-block img-fluid"
                alt="Logo"
                aria-label="Bank_Logo"
                width="100px"
                height="auto"
              />
            </b-navbar-brand>

              <b-navbar-nav>
                <b-row>
                  <!-- language dropdown -->
                  <b-col md="12">
                    <!-- text=EN will not fuction as it only render locally in the component, (this is Vue.js kind of thing) through function/ operation only will it work 'add btn label' -->
                    <b-dropdown id="langSwitcher" text="EN" right>
                      <template slot="button-content" v-if="selected">
                        <span v-for="(lang,i) in langs" :key="i" :value="lang">
                          <span slot="text" v-if="lang == language">
                            <img
                              :src="require('../assets/locales/locale_img/' + lang + '.png')"
                              width="21px"
                              height="14px"
                              class="img-fluid"
                            />
                            {{language}}
                          </span>
                        </span>
                      </template>
                      <template slot="button-content" v-else>
                         <span slot="text">{{this.$i18n.locale}}</span>
                      </template>

                      <!-- defines dropdown menu content -->
                      <b-dropdown-item
                        v-for="(lang,i) in langs"
                        :key="i"
                        :value="lang"
                        @click="languageSwitcher(lang)"
                        class="text-center"
                      >
                        <img
                          :src="require('../assets/locales/locale_img/' + lang + '.png')"
                          width="21px"
                          height="14px"
                        />
                        {{lang}}
                      </b-dropdown-item>
                    </b-dropdown>
                  </b-col>
                </b-row>
              </b-navbar-nav>
          </div>
        </b-navbar>
      </div>
    </header>
  </div>
</template>

<script>
export default {
  name: "navbar",
  data() {
    return {
      // reusable language tracking object
      langs: ["繁體中文", "日本語", "简体中文", "English", "한국어"],
      language: this.$t("language"),
      selected: false
    };
  },
  methods: {
    languageSwitcher(lang) {
      // remember langauge settings
      localStorage.setItem("lang", lang);
      this.selected = true;

      // set language based on selection
      this.$i18n.locale = lang;

      // pass chosen language to language tracker object > for downdown menu language/img display
      this.language = lang;

      // reload select
      // location.reload();

      //pass selected key.
      this.$emit('isSelected', this.selected);
    }
  },
  created() {
    //   on new start up, assign chosen langauge
    if (localStorage.getItem("lang") != null) {
      this.$i18n.locale = localStorage.getItem("lang");
      this.language = localStorage.getItem("lang");
      this.selected = true;

      
    }
  }
};
</script>

<style>
.dropdown-toggle.btn-secondary {
    color: black !important;
    background-color: transparent !important;
    border: none !important;
}
</style>




