import Vue from "vue";
import App from "./App.vue";

// import plugin functions
import BootstrapVue from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

import VModal from "vue-js-modal";

// import language switching function
import VueI18n from "vue-i18n";

// insert plugin/component into vue
Vue.use(BootstrapVue);
Vue.use(VModal);


Vue.use(VueI18n); // upon declare/ define vue to use; it's inserted into Vue

const messages = {
  简体中文: require("./assets/locales/简体中文.json"),
  繁體中文: require("./assets/locales/繁體中文.json"),
  English: require("./assets/locales/English.json"),
  日本語: require("./assets/locales/日本語.json"),
  한국어: require("./assets/locales/한국어.json")
};

const i18n = new VueI18n({
  locale: "简体中文",
  messages
});

Vue.config.productionTip = false;

new Vue({
  i18n,
  render: h => h(App)
}).$mount("#app");
